.search {
        width: 100%;
        margin: 0 auto;
        margin-bottom: 50px;
&-title {
        text-transform: uppercase;
        margin: 25px 0;
        font-weight: 300;
        font-size: 40px;
        line-height: 49px;
    }
   form {
       display: flex;
       align-items: center;

       input {
           height: 57px;
           margin-right: 15px;
           padding: 0 5px;
           border-radius: 5px;
           font-size: 20px;
           flex: 1 0 auto;
           background-color: #424242;
           color: #ffffff;
            border: none;
       }
   }
}
