.header {
    background-image: url(../../stories/assets/HeaderBackground.png);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 50px 30px 0;
    position: relative;
    text-align: center;
    width: 100%;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.5);
    }

    .header-container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-shrink: 0;
        position: relative;
        z-index: 1;
        width: 100%;
        padding-top: 10px;
    }

    .logo {
        height: 50px;
        margin-right: 20px;
        width: auto;
        position: absolute;
        top: 0px;
        left: 30px;

    }

    .search-container {
        flex-grow: 1;
        margin: 0 auto;
        max-width: 500px;
        width: 100%;
    }


      .add {

        height: 40px;
        width: 140px;
        text-transform: uppercase;
        color: #ffffff;
        background-color: #f65261;
        cursor: pointer;
        border-radius: 5px;
        border: none;
        font-weight: 600;

        &:hover {
            background-color: #f93143;
        }

        &:focus {
            box-shadow: 0 0 0 0.2rem #d87880;
        }
    }

    .genre-sort-controls {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
        z-index: 1;
    }
}