.dialog-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.dialog {
    background-color: rgba(42, 42, 42, 0.95);
    width: 80%;
    max-width: 600px;
    border-radius: 5px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
}

.dialog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid #eaeaea;
}

.dialog-title {
    margin: 0;
    font-size: 22px;
    font-weight: 600;
    color: #ffffff;
}

.dialog-close {
    background-color: transparent;
    border: none;
    font-size: 24px;
    font-weight: 600;
    color: #ffffff;
    cursor: pointer;
    transition: color 0.3s ease;
}

.dialog-close:hover {
    color: #eaeaea;
}

.dialog-body {
    padding: 20px;
}
